import React, { useState } from 'react';
import { Section, Card, Button } from '../components/ui';
import PageTransition from '../components/ui/PageTransition';
import { FadeInWhenVisible } from '../components/ui/ScrollAnimations';
import { trackFormSubmission } from '../utils/analytics';
import SEOHead from '../components/ui/SEOHead';

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });
  
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState(null);

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.name.trim()) {
      newErrors.name = 'Name is required';
    }
    
    if (!formData.email.trim()) {
      newErrors.email = 'Email is required';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email address';
    }
    
    if (!formData.subject.trim()) {
      newErrors.subject = 'Subject is required';
    }
    
    if (!formData.message.trim()) {
      newErrors.message = 'Message is required';
    } else if (formData.message.trim().length < 10) {
      newErrors.message = 'Message must be at least 10 characters long';
    }
    
    return newErrors;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrors({});
    
    const formErrors = validateForm();
    if (Object.keys(formErrors).length > 0) {
      setErrors(formErrors);
      return;
    }
    
    setIsSubmitting(true);
    setSubmitStatus(null);
    
    try {
      // Simulate form submission - replace with actual endpoint
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      setSubmitStatus('success');
      setFormData({ name: '', email: '', subject: '', message: '' });
      
      // Track successful form submission
      trackFormSubmission('Contact Form', true);
      
      // Clear success message after 5 seconds
      setTimeout(() => setSubmitStatus(null), 5000);
    } catch (error) {
      setSubmitStatus('error');
      
      // Track failed form submission
      trackFormSubmission('Contact Form', false);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear error for this field when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  return (
    <PageTransition>
      <SEOHead 
        title="Contact Us"
        description="Get in touch with datainamics for AI research collaborations, training inquiries, consulting services, and partnership opportunities."
        keywords="Contact datainamics, AI Consulting, AI Training Inquiry, Research Collaboration, AI Services"
        url="/contact"
      />
      <div className="contact-page">
        <Section className="contact-hero">
          <FadeInWhenVisible>
            <h1 className="page-title">Contact Us</h1>
            <p className="page-subtitle">
              Get in touch with our team for collaborations, training inquiries, or research partnerships.
            </p>
          </FadeInWhenVisible>
        </Section>

      <Section>
        <div className="contact-content">
          <div className="contact-info">
            <Card>
              <h3>Get in Touch</h3>
              <div className="contact-details">
                <div className="contact-item">
                  <strong>Email:</strong>
                  <a href="mailto:hello@datainamics.org">hello@datainamics.org</a>
                </div>
                
                <div className="contact-item">
                  <strong>Training Inquiries:</strong>
                  <a href="mailto:training@datainamics.org">training@datainamics.org</a>
                </div>
                
                <div className="contact-item">
                  <strong>Research Partnerships:</strong>
                  <a href="mailto:research@datainamics.org">research@datainamics.org</a>
                </div>
                
                <div className="contact-item">
                  <strong>Location:</strong>
                  <span>Morocco</span>
                </div>
              </div>
            </Card>

            <Card>
              <h3>Areas of Collaboration</h3>
              <ul className="collaboration-list">
                <li>AI Research Partnerships</li>
                <li>Training Program Development</li>
                <li>Industry Consulting</li>
                <li>Academic Collaborations</li>
                <li>Open Source Contributions</li>
                <li>Speaking Engagements</li>
              </ul>
            </Card>
          </div>

          <div className="contact-form-section">
            <Card>
              <h3>Send us a Message</h3>
              
              {submitStatus === 'success' && (
                <div className="success-message" role="alert" aria-live="polite">
                  Thank you for your message! We will get back to you soon.
                </div>
              )}
              
              {submitStatus === 'error' && (
                <div className="error-message" role="alert" aria-live="polite">
                  Sorry, there was an error sending your message. Please try again.
                </div>
              )}
              
              <form onSubmit={handleSubmit} className="contact-form" noValidate>
                <div className="form-group">
                  <label htmlFor="name">Name *</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    className={`form-input ${errors.name ? 'error' : ''}`}
                    aria-describedby={errors.name ? 'name-error' : undefined}
                    aria-invalid={errors.name ? 'true' : 'false'}
                  />
                  {errors.name && (
                    <span id="name-error" className="field-error" role="alert">
                      {errors.name}
                    </span>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="email">Email *</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    className={`form-input ${errors.email ? 'error' : ''}`}
                    aria-describedby={errors.email ? 'email-error' : undefined}
                    aria-invalid={errors.email ? 'true' : 'false'}
                  />
                  {errors.email && (
                    <span id="email-error" className="field-error" role="alert">
                      {errors.email}
                    </span>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="subject">Subject *</label>
                  <input
                    type="text"
                    id="subject"
                    name="subject"
                    value={formData.subject}
                    onChange={handleChange}
                    required
                    className={`form-input ${errors.subject ? 'error' : ''}`}
                    aria-describedby={errors.subject ? 'subject-error' : undefined}
                    aria-invalid={errors.subject ? 'true' : 'false'}
                  />
                  {errors.subject && (
                    <span id="subject-error" className="field-error" role="alert">
                      {errors.subject}
                    </span>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="message">Message *</label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    required
                    rows="5"
                    className={`form-textarea ${errors.message ? 'error' : ''}`}
                    aria-describedby={errors.message ? 'message-error' : undefined}
                    aria-invalid={errors.message ? 'true' : 'false'}
                    placeholder="Tell us about your project or inquiry..."
                  />
                  {errors.message && (
                    <span id="message-error" className="field-error" role="alert">
                      {errors.message}
                    </span>
                  )}
                </div>

                <Button 
                  type="submit" 
                  variant="primary" 
                  size="large"
                  disabled={isSubmitting}
                  ariaLabel={isSubmitting ? 'Sending message...' : 'Send message'}
                >
                  {isSubmitting ? 'Sending...' : 'Send Message'}
                </Button>
              </form>
            </Card>
          </div>
        </div>
      </Section>
    </div>
    </PageTransition>
  );
};

export default Contact;
